.perform-action {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

.nft-display-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 20px;
  justify-content: center;
}

.nft-display {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 10px;
  background-color: #fff;
  max-width: 200px; /* Adjust this to control the size of each box */
  text-align: center;
  flex: 1; /* Ensures the box scales properly within the container */
}

.nft-display img.nft-image {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  margin-top: 10px;
  max-height: 100px; /* Adjust this to control the height of the image */
}

.nft-display h3 {
  font-size: 14px;
  margin: 10px 0 5px;
}

.nft-display p {
  font-size: 12px;
  margin: 5px 0;
}

.progress-container {
  margin-top: 10px;
}

.progress-label {
  font-size: 12px;
  margin-top: 5px;
}

.refill-button {
  margin-top: 10px;
  font-size: 12px;
  padding: 5px 10px;
}

.plot-select {
  margin-bottom: 20px;
}

@media (max-width: 768px) {
  .nft-display {
    max-width: 150px; /* Make containers smaller on mobile */
  }

  .nft-display img.nft-image {
    max-height: 80px; /* Adjust the image size on mobile */
  }
}

.alert {
  margin-top: 20px;
}
